<h2>Instance Properties</h2>
<!-- prettier-ignore -->
<table>
<thead>
<tr>
<th>name</th>
<th>type</th>
<th>default</th>
<th>description</th>
</tr>
</thead>
<tbody>
<tr>
<td>backgroundColor</td>
<td>Object</td>
<td>Array</td>
<td>String</td>
</tr>
<tr>
<td>backgroundPadding</td>
<td>Object</td>
<td></td>
<td><code v-pre="">optional</code> Gets or sets the background padding, in pixels, of this label. The x value controls horizontal padding, and the y value controls vertical padding. <strong>Structure: { x: number, y: number }</strong></td>
</tr>
<tr>
<td>disableDepthTestDistance</td>
<td>Number</td>
<td></td>
<td><code v-pre="">optional</code> Gets or sets the distance from the camera at which to disable the depth test to, for example, prevent clipping against terrain. When set to zero, the depth test is always applied. When set to Number.POSITIVE_INFINITY, the depth test is never applied.</td>
</tr>
<tr>
<td>distanceDisplayCondition</td>
<td>Object</td>
<td></td>
<td><code v-pre="">optional</code> Gets or sets the condition specifying at what distance from the camera that this label will be displayed. <strong>Structure: { near: number, far: number }</strong></td>
</tr>
<tr>
<td>eyeOffset</td>
<td>Object</td>
<td><code v-pre="">{x: 0, y: 0, z: 0}</code></td>
<td><code v-pre="">optional</code> Gets and sets the 3D Cartesian offset applied to this label in eye coordinates. Eye coordinates is a left-handed coordinate system, where x points towards the viewer's right, y points up, and z points into the screen. Eye coordinates use the same scale as world and model coordinates, which is typically meter. <strong>Structure{ x: number, y: number, z: number }</strong></td>
</tr>
<tr>
<td>fillColor</td>
<td>Object|String|Array</td>
<td><code v-pre="">white</code></td>
<td><code v-pre="">optional</code> Gets or sets the fill color of this label.</td>
</tr>
<tr>
<td>font</td>
<td>String</td>
<td><code v-pre="">'30px sans-serif'</code></td>
<td><code v-pre="">optional</code>Gets or sets the font used to draw this label. Fonts are specified using the same syntax as the CSS 'font' property.</td>
</tr>
<tr>
<td>heightReference</td>
<td>Number</td>
<td><code v-pre="">0</code></td>
<td><code v-pre="">optional</code> Gets or sets the height reference of this billboard. <strong>NONE: 0, CLAMP_TO_GROUND: 1, RELATIVE_TO_GROUND: 2</strong></td>
</tr>
<tr>
<td>horizontalOrigin</td>
<td>Number</td>
<td><code v-pre="">0</code></td>
<td><code v-pre="">optional</code> Gets or sets the horizontal origin of this label, which determines if the label is drawn to the left, center, or right of its anchor position. <strong>CENTER: 0, LEFT: 1, RIGHT: -1</strong></td>
</tr>
<tr>
<td>id</td>
<td>*</td>
<td></td>
<td><code v-pre="">optional</code> Gets or sets the user-defined value returned when the label is picked.</td>
</tr>
<tr>
<td>outlineColor</td>
<td>Object</td>
<td>Array</td>
<td>String</td>
</tr>
<tr>
<td>outlineWidth</td>
<td>Number</td>
<td><code v-pre="">0</code></td>
<td><code v-pre="">optional</code> Gets or sets the outline width of this label.</td>
</tr>
<tr>
<td>pixelOffset</td>
<td>Object</td>
<td><code v-pre="">{x: 0, y: 0}</code></td>
<td><code v-pre="">optional</code> Gets or sets the pixel offset in screen space from the origin of this label. This is commonly used to align multiple labels and billboards at the same position, e.g., an image and text. The screen space origin is the top, left corner of the canvas; x increases from left to right, and y increases from top to bottom. <strong>Structure{ x: number, y: number }</strong></td>
</tr>
<tr>
<td>pixelOffsetScaleByDistance</td>
<td>Object</td>
<td></td>
<td><code v-pre="">optional</code> Gets or sets near and far pixel offset scaling properties of a Label based on the Label's distance from the camera. A label's pixel offset will be scaled between the NearFarScalar#nearValue and NearFarScalar#farValue while the camera distance falls within the upper and lower bounds of the specified NearFarScalar#near and NearFarScalar#far. Outside of these ranges the label's pixel offset scaling remains clamped to the nearest bound. If undefined, pixelOffsetScaleByDistance will be disabled. <strong>Structure{ near: number, nearValue: number, far: number, farValue: number }</strong></td>
</tr>
<tr>
<td>position</td>
<td>Object</td>
<td></td>
<td><code v-pre="">optional</code> Gets or sets the Cartesian position of this label. <strong>Structure{ lng: number, lat: number, height: number }</strong></td>
</tr>
<tr>
<td>scale</td>
<td>Number</td>
<td><code v-pre="">1.0</code></td>
<td><code v-pre="">optional</code> Gets or sets the uniform scale that is multiplied with the label's size in pixels. A scale of 1.0 does not change the size of the label; a scale greater than 1.0 enlarges the label; a positive scale less than 1.0 shrinks the label.</td>
</tr>
<tr>
<td>scaleByDistance</td>
<td>Object</td>
<td></td>
<td><code v-pre="">optional</code> Gets or sets near and far scaling properties of a Label based on the label's distance from the camera. A label's scale will interpolate between the NearFarScalar#nearValue and NearFarScalar#farValue while the camera distance falls within the upper and lower bounds of the specified NearFarScalar#near and NearFarScalar#far. Outside of these ranges the label's scale remains clamped to the nearest bound. If undefined, scaleByDistance will be disabled. <strong>Structure { near: number, nearValue: number, far: number, farValue: number }</strong></td>
</tr>
<tr>
<td>show</td>
<td>Boolean</td>
<td><code v-pre="">true</code></td>
<td><code v-pre="">optional</code> Determines if this label will be shown. Use this to hide or show a label, instead of removing it and re-adding it to the collection.</td>
</tr>
<tr>
<td>showBackground</td>
<td>Boolean</td>
<td><code v-pre="">false</code></td>
<td><code v-pre="">optional</code> Determines if a background behind this label will be shown.</td>
</tr>
<tr>
<td>text</td>
<td>String</td>
<td></td>
<td><code v-pre="">optional</code> Gets or sets the text of this label.</td>
</tr>
<tr>
<td>totalScale</td>
<td>Number</td>
<td><code v-pre="">1.0</code></td>
<td><code v-pre="">optional</code> Gets the total scale of the label, which is the label's scale multiplied by the computed relative size of the desired font compared to the generated glyph size.</td>
</tr>
<tr>
<td>translucencyByDistance</td>
<td>Object</td>
<td></td>
<td><code v-pre="">optional</code> Gets or sets near and far translucency properties of a Label based on the Label's distance from the camera. A label's translucency will interpolate between the NearFarScalar#nearValue and NearFarScalar#farValue while the camera distance falls within the upper and lower bounds of the specified NearFarScalar#near and NearFarScalar#far. Outside of these ranges the label's translucency remains clamped to the nearest bound. If undefined, translucencyByDistance will be disabled. <strong>Structure { near: number, nearValue: number, far: number, farValue: number }</strong></td>
</tr>
<tr>
<td>verticalOrigin</td>
<td>Number</td>
<td><code v-pre="">0</code></td>
<td><code v-pre="">optional</code> Gets or sets the vertical origin of this label, which determines if the label is to the above, below, or at the center of its anchor position. <strong>CENTER: 0, BOTTOM: 1, BASELINE: 2, TOP: -1</strong></td>
</tr>
<tr>
<td>labelStyle</td>
<td>Number</td>
<td><code v-pre="">0</code></td>
<td><code v-pre="">optional</code> A Property specifying the LabelStyle. <strong>FILL: 0, OUTLINE: 1, FILL_AND_OUTLINE: 2</strong></td>
</tr>
</tbody>
</table>
<hr>
<ul>
<li>Refer to the official document: <strong><a href="https://cesium.com/docs/cesiumjs-ref-doc/Label.html">Label</a></strong></li>
</ul>
