<td>click</td>
<td>{button,surfacePosition,pickedFeature,type,windowPosition}</td>
<td>Triggered when the mouse clicks on the primitive.</td>
</tr>
<tr>
<td>clickout</td>
<td>{button,surfacePosition,pickedFeature,type,windowPosition}</td>
<td>Touch when the mouse clicks outside the primitive.</td>
</tr>
<tr>
<td>dblclick</td>
<td>{button,surfacePosition,pickedFeature,type,windowPosition}</td>
<td>Triggered when the left mouse button double-clicks the primitive.</td>
</tr>
<tr>
<td>mousemove</td>
<td>{button,surfacePosition,pickedFeature,type,windowPosition}</td>
<td>Triggered when the mouse moves on this primitive.</td>
</tr>
<tr>
<td>mouseover</td>
<td>{button,surfacePosition,pickedFeature,type,windowPosition}</td>
<td>Triggered when the mouse moves to this primitive.</td>
</tr>
<tr>
<td>mouseout</td>
<td>{button,surfacePosition,pickedFeature,type,windowPosition}</td>
<td>Triggered when the mouse moves out of the primitive.</td>
</tr>
</tbody>
</table>
